<template>
  <div id="note-detail">
    <head-top title="笔记详情"></head-top>
    <div class="main">
      <div class="tt">{{title || '笔记标题'}}</div>
      <r-quill-editor
        ref="REditor"
        :funType="funType"
        :content="content"
        :is-edit="false"
        :max-height="maxHeight"
      ></r-quill-editor>
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";
import RQuillEditor from "@/components/RQuillEditor";
import { ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: { headTop, RQuillEditor },
  props: {},
  data() {
    return {
      title: this.$route.query.title || "",
      content: this.$route.query.content || "",
      funType: ModulePicture.StudentNote,
      maxHeight: null
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      // 更新高度
      setTimeout(() => {
        let title = document.getElementsByClassName("tt");
        if (title[0]) title = title[0];
        this.maxHeight =
          "calc(100vh - 4.5rem - " +
          (title ? title.clientHeight - 2 : 0) +
          "px)";
      }, 500);
    });
  },
  watch: {},
  methods: {}
};
</script>

<style lang='less' scoped>
#note-detail {
  width: 100%;
  height: 100%;
  background: #ebf0ef;
  .main {
    width: 100%;
    text-align: left;

    word-break: break-all;
    word-wrap: break-word;
    .tt {
      text-align: center;
      font-size: 1.4rem;
      font-weight: bolder;
      color: rgba(51, 51, 51, 1);
      padding:1rem 0;
      margin: 0 3vw;
      position: relative;
      &::after {
        content: " ";
        position: absolute;
        bottom: 0;
        left: 0;
        height: 1px;
        width: 100%;
        transform: scaleY(0.1);
        background-color: #000;
      }
    }
    .content {
      white-space: pre-wrap;
      white-space: -moz-pre-wrap;
      white-space: -pre-wrap;
      white-space: -o-pre-wrap;
    }

    /deep/ .quill-editor {
      background: #ebf0ef;
    }
  }
}
</style>


